document.addEventListener("DOMContentLoaded", function () {
  inicairApp();
});
function inicairApp() {
  crearGaleria();
  scrollNav();
  fijarBarra();
}
function scrollNav() {
  const scrollenlaces = document.querySelectorAll(".navegacion__principal a");
  scrollenlaces.forEach((enlace) => {
    enlace.addEventListener("click", function (e) {
      e.preventDefault();
      const direccion = e.target.attributes.href.value;

      const seccion = document.querySelector(direccion);
      console.log(seccion);
      seccion.scrollIntoView({ behavior: "smooth" });
    });
  });
}
function fijarBarra() {
  const barra = document.querySelector(".header");
  const sobreFestival = document.querySelector(".sobre__festival");
  window.addEventListener("scroll", function () {
    if (sobreFestival.getBoundingClientRect().top < 0) {
      barra.classList.add('fijar__barra');
    } else {
      barra.classList.remove('fijar__barra');
    }
  });
}
function crearGaleria() {
  const galeria = document.querySelector(".galeria__imagenes");
  for (let i = 1; i <= 12; i++) {
    const imagen = document.createElement("picture");
    imagen.innerHTML = `
          <source srcset="build/img/thumb/${i}.avif" type="imagen/avif" />
          <source srcset="build/img/thumb/${i}.webp" type="imagen/webp" />
          <img
            loading="lazy"
            width="200"
            height="300"
            src="build/img/thumb/${i}.jpg"
            alt="imagen galeria"/>
         `;
    imagen.onclick = function () {
      mostrarImagen(i);
    };
    galeria.appendChild(imagen);
  }
}
function mostrarImagen(id) {
  const imagen = document.createElement("picture");
  imagen.innerHTML = `
          <source srcset="build/img/grande/${id}.avif" type="imagen/avif" />
          <source srcset="build/img/grande/${id}.webp" type="imagen/webp" />
          <img
            loading="lazy"
            width="200"
            height="300"
            src="build/img/grande/${id}.jpg"
            alt="imagen galeria"/>
         `;
  //Crea el overlay con la imagen
  const overlay = document.createElement("DIV");
  overlay.appendChild(imagen);
  overlay.classList.add("overlay");
  overlay.onclick = function () {
    const body = document.querySelector("body");
    body.classList.remove("fijar-body");
    overlay.remove();
  };
  //Cerrando ventana modal
  const cerrandoModal = document.createElement("P");
  cerrandoModal.textContent = "X";
  cerrandoModal.classList.add("btn-cerrar");
  cerrandoModal.onclick = function () {
    const body = document.querySelector("body");
    body.classList.remove("fijar-body");
    overlay.remove();
  };
  overlay.appendChild(cerrandoModal);

  //AÃ±adiendo overlay al html
  const body = document.querySelector("body");
  body.appendChild(overlay);
  body.classList.add("fijar-body");
}
